@import url("https://fonts.googleapis.com/css2?family=Redressed&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  color: rgb(50, 50, 50);
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  /* --nav-font: "Lobster", cursive; */
  --nav-font: "Redressed", cursive;
  --bg-color: rgb(0, 65, 0);
  --text-shadow-color: rgb(31, 27, 0);
  --bg-shadow-color: rgba(0, 0, 0, 0.7);
  --page-bg-color: rgba(255, 255, 255, 0.9);
  --link-text-color: white;
  --fadedgreen: rgb(120, 140, 120);
  --whitegreen: rgb(200, 230, 200);
  --titlegrey: rgb(
    130,
    140,
    130
  ); /* for titles visited on blog list view && tags background*/
  --title: rgb(200, 200, 200);
  z-index: -10;
  background-color: rgb(150, 150, 150);
}
